import { pathMatch } from './pathMatch';
import { permuteDomain } from './permuteDomain';
import { Store } from './store';

export const MemoryCookieStore = function () {
  // eslint-disable-next-line prefer-reflect
  Store.call(this);
  this.idx = {};
};

MemoryCookieStore.prototype = Object.create(Store.prototype);

MemoryCookieStore.prototype.idx = null;

// Since it's just a struct in RAM, this Store is synchronous
MemoryCookieStore.prototype.synchronous = true;

// force a default depth:
MemoryCookieStore.prototype.inspect = function () {
  return JSON.stringify(this.idx, null, 2);
};

MemoryCookieStore.prototype.findCookie = function (domain, path, key, cb) {
  if (!this.idx[domain]) {
    return cb(null, undefined);
  }
  if (!this.idx[domain][path]) {
    return cb(null, undefined);
  }

  return cb(null, this.idx[domain][path][key] || null);
};

MemoryCookieStore.prototype.findCookies = function (domain, path, cb) {
  const results = [];

  if (!domain) {
    return cb(null, []);
  }

  let pathMatcher;

  if (!path) {
    // null means "all paths"
    pathMatcher = function matchAll (domainIndex) {
      for (const curPath in domainIndex) {
        if (domainIndex.hasOwnProperty(curPath)) {
          const pathIndex = domainIndex[curPath];

          for (const key in pathIndex) {
            if (pathIndex.hasOwnProperty(key)) {
              results.push(pathIndex[key]);
            }
          }
        }
      }
    };
  } else {
    pathMatcher = function matchRFC (domainIndex) {
       // NOTE: we should use path-match algorithm from S5.1.4 here
       // (see : https://github.com/ChromiumWebApps/chromium/blob/b3d3b4da8bb94c1b2e061600df106d590fda3620/net/cookies/canonical_cookie.cc#L299)
      Object.keys(domainIndex).forEach(cookiePath => {
        if (pathMatch(path, cookiePath)) {
          const pathIndex = domainIndex[cookiePath];

          for (const key in pathIndex) {
            if (pathIndex.hasOwnProperty(key)) {
              results.push(pathIndex[key]);
            }
          }
        }
      });
    };
  }

  const domains = permuteDomain(domain) || [ domain ];
  const idx = this.idx;

  domains.forEach(curDomain => {
    const domainIndex = idx[curDomain];

    if (!domainIndex) {
      return;
    }
    pathMatcher(domainIndex);
  });

  cb(null, results);
};

MemoryCookieStore.prototype.putCookie = function (cookie, cb) {
  if (!this.idx[cookie.domain]) {
    this.idx[cookie.domain] = {};
  }

  if (!this.idx[cookie.domain][cookie.path]) {
    this.idx[cookie.domain][cookie.path] = {};
  }

  this.idx[cookie.domain][cookie.path][cookie.key] = cookie;
  cb(null);
};

MemoryCookieStore.prototype.updateCookie = function (oldCookie, newCookie, cb) {
  // updateCookie() may avoid updating cookies that are identical.  For example,
  // lastAccessed may not be important to some stores and an equality
  // comparison could exclude that field.
  this.putCookie(newCookie, cb);
};

MemoryCookieStore.prototype.removeCookie = function (domain, path, key, cb) {
  if (this.idx[domain] && this.idx[domain][path] && this.idx[domain][path][key]) {
    // eslint-disable-next-line prefer-reflect
    delete this.idx[domain][path][key];
  }
  cb(null);
};

MemoryCookieStore.prototype.removeCookies = function (domain, path, cb) {
  if (this.idx[domain]) {
    if (path) {
      // eslint-disable-next-line prefer-reflect
      delete this.idx[domain][path];
    } else {
      // eslint-disable-next-line prefer-reflect
      delete this.idx[domain];
    }
  }

  return cb(null);
};

MemoryCookieStore.prototype.getAllCookies = function (cb) {
  const cookies = [];
  const idx = this.idx;
  const domains = Object.keys(idx);

  domains.forEach(domain => {
    const paths = Object.keys(idx[domain]);

    paths.forEach(path => {
      const keys = Object.keys(idx[domain][path]);

      keys.forEach(key => {
        if (key !== null) {
          cookies.push(idx[domain][path][key]);
        }
      });
    });
  });

  // Sort by creationIndex so deserializing retains the creation order.
  // When implementing your own store, this SHOULD retain the order too
  cookies.sort((objA, objB) => (objA.creationIndex || 0) - (objB.creationIndex || 0));

  cb(null, cookies);
};



